import collageapi from 'api/collage';

function verifyPromises(uploadPromises) {
  let status;
  for (let i = 0; i < uploadPromises.length; i += 1) {
    if (
      uploadPromises[i].status === 'fulfilled' ||
      uploadPromises[i].value.ok === true
    ) {
      status = true;
    } else {
      status = false;
      break;
    }
  }
  return status;
}

function uploadCollageImages(
  thumbnailCanvas,
  imageURLS,
  uploadCallback,
  images,
  collageID
) {
  const imagePromises = Object.keys(imageURLS).map(async (item) => {
    // Upload thumbnail
    const thumbnailKey = `thumbnail-${collageID}.jpg`;
    if (item === thumbnailKey) {
      return thumbnailCanvas.toBlob(async (blob) =>
        collageapi.upload(imageURLS[thumbnailKey], blob)
      );
    }

    // Upload Images
    const url = imageURLS[item];
    const Img = images.find((img) => img.title === item);
    return collageapi.upload(url, Img.file).then((resp) => {
      uploadCallback(); // Increase image upload count
      return resp;
    });
  });

  return imagePromises;
}

